export class QrcodeModel {

  public version: number;   //  1.0
  public qrcode: string;    // 
  public amount: number;    // 
  public orderId: number;   // 
  public payfees: number;   // 1 sender 2 receiver
  public coinID:string;


  constructor() {
    this.version = 1.0;
    this.amount = 0;
    this.payfees = 1;
  }


}



